.circle {
    width: 44px;
    height: 44px;
    flex-grow: 0;
    margin: 0 6px 12px;
    border-radius: 100%;
    padding: 12px 17px 11px;
    background-color: var(--white);
  }
  .borderCircle {
    border: solid 1.5px var(--black);
  }
  .marginFieldNew {
    margin-bottom: 2.5rem;
  }
  .scrollNew{height: calc(100vh - 24rem); overflow-y: scroll;}